  border-top:1px solid #d5d5d5;
  font-size: 3.3rem;
  margin: 0;
  text-align: left;
  padding: 2rem 4rem;
}

.ref p {
  margin: 2px;
}

dl {
  margin: 1vh 0;
}

dd {
  margin-left: 3rem;
}

.expander {
  font-size: 1.2em;
}

.given-name-char:active {
  color: silver;
}



