header {
  position: fixed;
  top:0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  height: 7vh;
  width: 100rem;
  padding: 2rem;
  background-color: white;
  z-index: 1;
  border-bottom: 1px solid #d5d5d5;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

header * {
  padding: 0;
  margin: 0;
}

header i:active {
  color: grey;
}

header h1 {
  font-size:5rem;
  color:#2bb;
}

header h2 {
  font-size:5rem;
  color:black;
}

header h3 {
  font-size:4.5rem;
  font-weight: 400;
  color:black;
}

header p {
  font-size: 3.5rem;
}

header p a {
  text-decoration: underline;
}

header .icon {
  color: #555;
  font-size:6rem;
  padding: 0 0.6rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.placeholder {
  opacity: 0;
}

.fake-header {
  display: block;
  height: 7vh;
}




