ol {
  padding: 0;
}

section {
  margin:7rem 0;
  background-color: white;
  border-top:1px solid #e5e5e5;
  border-bottom:1px solid #e5e5e5;
}

li {
  margin-left: 5rem;
  padding: 2rem 3.3rem 1.7rem 0;
  list-style: none;
  border-bottom: 1px solid #eee;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

li:last-child {
  border: none;
  padding-bottom: 1.8rem;
}

a:not(:last-child) li {
  border-bottom: 1px solid #eee;

}
.menu li:active {
  background-color: transparent;
  color: silver;
}

a {
  color: black;
  text-decoration: none;
}

a:active {
  color: silver;
  background-color: transparent;
}



